import React, { useEffect, useState, useContext } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import {
  CardContent,
  CardActions,
  Collapse,
  Card,
  Chip,
  CardHeader,
  CardMedia,
  CircularProgress,
  Avatar,
  IconButton,
  Typography,
} from "@material-ui/core";
import { red } from "@material-ui/core/colors";
import FavoriteIcon from "@material-ui/icons/Favorite";
import ShareIcon from "@material-ui/icons/Share";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import ModeCommentIcon from "@material-ui/icons/ModeComment";
import { firestore } from "../../config/firebase";
import { UserContext } from "../../config/UserContext";
import Comment from "../comment/Comment";

const Post = ({ owner, date, description, image, project, id }) => {
  const currentUser = useContext(UserContext);
  const [expanded, setExpanded] = useState(false);
  const [comments, setComments] = useState([]);
  const [comment, setComment] = useState("");
  const [expComment, setExpComment] = useState(false);

  const [currentProject, setcurrentProject] = useState();
  const [ownerDisplayName, setOwnerDisplayName] = useState();
  const [ownerPhoto, setOwnerPhoto] = useState();
  const [like, setLike] = useState("#757575");
  const [docId, setDocId] = useState();

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const handleClick = () => {
    console.info("You clicked the Chip.");
  };

  useEffect(() => {
    if (project) {
      firestore
        .collection("projects")
        .doc(project)
        .get()
        .then((doc) => {
          if (doc) {
            setcurrentProject(doc.data().project);
          }
        });
    }
    if (owner) {
      firestore
        .collection("users")
        .doc(owner)
        .get()
        .then((doc) => {
          if (doc) {
            setOwnerDisplayName(doc.data().user);
            setOwnerPhoto(doc.data().photo);
          }
        });
    }

    if (id && currentUser.uid) {
      firestore
        .collection("likes")
        .where("postId", "==", id)
        .where("uid", "==", currentUser.uid)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            if (doc.exists) {
              setDocId(doc.id);
              setLike("#ff3838");
            }
          });
        });
    }

    firestore
      .collection("comments")
      .where("postId", "==", id)
      .get()
      .then((snapshot) => {
        if (snapshot) {
          let data = [];
          snapshot.forEach((item) => {
            data.push({ ...item.data(), id: item.id });
          });
          setComments(data);
        }
      });
  });

  const handleFavorite = () => {
    if (!docId) {
      firestore.collection("likes").add({
        postId: id,
        uid: currentUser.uid,
      });
      setLike("#ff3838");
    } else {
      firestore.collection("likes").doc(docId).delete();
      setLike("#757575");
      setDocId(null);
    }
  };

  const handleExpComment = () => {
    setExpComment(!expComment);
  };
  const handleComment = (currentComment) => {
    if (currentComment !== "") {
      firestore.collection("comments").add({
        postId: id,
        uid: currentUser.uid,
        user: currentUser.email.split("@")[0],
        comment: currentComment,
      });
    }
    //setComment(currentComment);
    setExpComment(!expComment);
    //console.log(comment);
  };

  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar
            aria-label="recipe"
            className={classes.avatar}
            src={ownerPhoto}
          />
        }
        action={
          <IconButton aria-label="settings">
            {/* <MoreVertIcon /> */}
          </IconButton>
        }
        title={ownerDisplayName ? ownerDisplayName : ""}
        // subheader={date}
      />
      {image ? (
        <CardMedia className={classes.media} image={image} title="" />
      ) : (
        <CircularProgress />
      )}
      <CardContent>
        {currentProject && (
          <div className={classes.chipList}>
            <Chip
              className={classes.chip}
              size="small"
              label={"#" + currentProject}
              onClick={handleClick}
            />
          </div>
        )}
        <Typography variant="body2" color="textSecondary" component="p">
          {description}
        </Typography>
        <Typography variant="caption" color="textSecondary" component="p">
          {date}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites" onClick={handleFavorite}>
          <FavoriteIcon style={{ fill: like }} />
        </IconButton>
        {/* <IconButton aria-label="share">
          <ShareIcon />
        </IconButton> */}
        <IconButton aria-label="comment" onClick={handleExpComment}>
          <ModeCommentIcon />
        </IconButton>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      {expComment && (
        <div className={classes.comment}>
          <Comment handleComment={handleComment}></Comment>
        </div>
      )}
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          {comments.length > 0
            ? comments.map((data, index) => {
                return (
                  <div key={index}>
                    <Typography
                      variant="subtitle2"
                      color="textSecondary"
                    ></Typography>
                    <Typography variant="body2" color="textSecondary" paragraph>
                      <b>{data.user}</b> {data.comment}
                    </Typography>
                  </div>
                );
              })
            : ""}
        </CardContent>
      </Collapse>
    </Card>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: "hidden",
    // padding: theme.spacing(10, 3),
    // height: "100%",
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500,
  },
  chip: {
    backgroundColor: "#f7ac97",
    margin: "0.5vh 1vh 0.5vh 0",
  },
  chipList: {
    display: "flex",
    justifyContent: "left",
    flexWrap: "wrap",
    padding: "1vh 2vh 1vh 0",
  },
  media: {
    height: 200,
    paddingTop: "56.25%", // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
  },
  comment: {
    display: "flex",
    flexWrap: "wrap",
    padding: "0px 15px 15px 15px",
  },
}));

export default Post;
